body{
  max-width:100%;
}
*{
  margin:0;
}
nav{
  display:flex;
  align-items: center;

  padding:20px 120px;
  background-color: #cc3f3f;
  color: white;
  box-shadow: 0 8px 6px -6px black;
}
.nav--logo{
  font-size: 30px;
  margin-right: 5px;
}
nav h1{
  font-size: 20px;
}
section{
  padding:20px 10px;
  display: flex;
  height: 400px;
}
.section--img{
  height:400px;
  width:250px;
  margin-right: 10px;
  border-radius: 9px;
  pointer-events: none;
box-shadow: 0 3px 10px rgb(0 0 0 / 0.4);
}
section a{
         color:#9f9f9f;

}
 section a:hover{
  color: black;
}
.place--div{
  padding-top:10px;

}
.place--loc_name{
        display:flex;
        align-items: center;

}
.place--name{
  margin-right: 10px;
}
.place--experience{
  padding-right: 20px;
}